let DefaultValueUtility={
    formatText:function (type,text) {
        //debugger;
        if(type=="Const"){
            return ":"+text+"";
        }
        else if(type=="EnvVar"){
            return ":"+text+"";
        }
        else if (type == "") {
            return "";
        }
        return ""+text;
    }
}

export { DefaultValueUtility as default};